import React from 'react'
import {NavLink, useLocation} from 'react-router-dom'
import Whales from '../../../images/Whale.png'
import Calculator from '../../../images/Calculator.png'
import Holder from '../../../images/Holders.png'
import Wallet from '../../../images/Wallet.png'
import Home from '../../../images/Home.png'

const Nav = () => {
    const location = useLocation();
    const {pathname} = location;
    const splitLocation = pathname.split("/");

    return (
        <div className="align-center">
            <NavLink to="/"
                     className={splitLocation[1] === "" ? "choosen nav-button" : "nav-button"}>
            <img src={Home} alt="Home" />
            </NavLink>
            <NavLink to="/Whales"
                     className={splitLocation[1] === "Whales" ? "choosen nav-button" : "nav-button"}>
            <img src={Whales} alt="Whales" />
            </NavLink>
            <NavLink to="/Calculator"
                     className={splitLocation[1] === "Calculator" ? "choosen nav-button" : "nav-button"}>
            <img src={Calculator} alt="Calculator" />
            </NavLink>
            <NavLink to="/Holders"
                     className={splitLocation[1] === "Holders" || splitLocation[1] === "Holder" ? "choosen nav-button" : "nav-button"}>
            <img src={Holder} alt="Holders" />
            </NavLink>
            <NavLink to="/Tracker"
                     className={splitLocation[1] === "Tracker" ? "choosen nav-button" : "nav-button"}>
            <img src={Wallet} alt="Wallets" />
            </NavLink>
        </div>
    )
}

export default Nav
