import React from 'react'


import Text from './../Form/Text'

const Input = (props) => {
    const UpdateInput = (e) => {
        props.UpdateWallet(e.target.value)
    }

    return(
        <div className={props.Classes + " tracker-element__input margin-bottom flex flex-column"}>
            <div>
                <Text>
                    {props.Error && <h2 className="Negativ border-radius padding">{props.Error}</h2>}
                    {props.Message && <h2 className="Positiv border-radius padding">{props.Message}</h2>}
                    <h3>
                        {props.Title}
                    </h3>
                    <p className="table__subtext">{props.Metric}</p>
                </Text>
            </div>
            <div className="tracker__container__input flex flex-column align-center">
                <input type={props.Type} id={props.Title.toString().replace(' ', '')} step={props.Increment} min={props.Minimum} max={props.Maximim} onChange={UpdateInput} placeholder={props.PlaceHolder} defaultValue={props.SetValue}/>
                {props.Message && <button className="tracker-expand-button" onClick={props.OnClickFunction} >{props.ButtonText}</button>}
            </div>
        </div>
    )
}

export default Input